export default{
	data(){
		return{
			page:1,
			hasMore:true,
			data_list:[]
		}
		
	},
	methods:{
		getMore(){
			this.page ++;
			this.getList()
		},
		async getList(){
			let res = await this.$http({
				apiName:"referList",
				data:{
					page:this.page,
				}
			})
			try{
				this.data_list = this.data_list.concat(res.list);
				this.hasMore = res.hasNextPage
			}catch(e){}
		},
	},
	async onPullDownRefresh() {
		uni.showLoading({
			title:"数据加载中..."
		})
		this.page = 1;
		this.data_list = [];
		await this.getList()
		uni.stopPullDownRefresh();
		uni.hideLoading();
	},
 	async onLoad() {
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.getList()
		uni.hideLoading();
	}
}
